/* IMPORTS */

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400&family=Roboto:wght@500&display=swap');


/* HELPERS */

@mixin show {
    border: 2px dashed lightcoral;
}


/* COLOR PALETTE */

$color1: #212227;
$color2: #E09F3E;
$color3: #1985A1;
$color4: #EEEEEE;
$color5: #E2EFDE;


/* COLORS */

$background-color: $color1;
$primary-color: $color2;
$secondary-color: $color3;
$terceary-color: $color5;

$second-background-color: $color3;
$light-background-color: $color4;

$section1-background-color: $background-color;
$section2-background-color: $light-background-color;
$border-color: $color5;

$link-color: $color3;
$hover-link-color: $color5;

$profile-border-color: $primary-color;


/* CONSTANTS */

$width-threshold: 900px;


/* RESET */

html, body {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}


/* LAYOUT */

.wrapper {
    width: $width-threshold;
    margin: 0 auto;
}

header {
    min-height: 100vh;
}

.greeting-wrapper {
    min-height: 95vh;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas:
        'greeting_item_left greeting_item_right';

    align-items: center;

    .greeting-item-left {
        grid-area: greeting_item_left;
    }
    
    .greeting-item-right {
        grid-area: greeting_item_right;
    }

    .links {
        display: flex;
        justify-content: flex-start;
        padding: 0;
        margin: 0;
    
        li {
            margin-right: 20px;    
        }
    }

}

#navigation-bar {
    position: fixed;
    top: 0px;
    width: 100%;

    nav {
        ul {
            display: flex;
            justify-content: flex-end;
            margin: 0;

            li {
                margin-left: 10px;
                text-align: center;

                a {
                    display: block;
                }
            }
        }
    }
}

.form-wrapper {
    display: grid;
    justify-content: center;
    padding: 50px;
}

.footer-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


/* TYPOGRAPHY */

h1, h2, h3, h4, h5, h6, strong {
    color: $primary-color;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: 1em;
}

p, li, span, label, input, textarea {
    color: $secondary-color;
    font-family: 'Roboto Mono', monospace;
    font-weight: 400;
    line-height: 1.2em;
}

h1 { font-size: 56px; }
h2 { font-size: 36px; }
h3 { font-size: 28px; }
h4 { font-size: 24px; }
h5 { font-size: 20px; }
h6 { font-size: 16px; }


/* STYLING */

a {
    color: $link-color;
}

a:hover {
    color: $hover-link-color;
}

.section-0 {
    overflow: auto;
}

.section-1 {
    background-color: $section1-background-color;
    border-bottom: 1px solid $border-color;
    overflow: auto;
}

.section-2 {
    background-color: $section2-background-color;
    border-bottom: 1px solid $border-color;
    overflow: auto;
}

.greeting-wrapper {
    #profile-photo {
        border-radius: 50%;
        border: 4px solid $profile-border-color;
        width: 200px;
        height: 200px;
        margin: 50px;
        object-fit: cover;

        -webkit-box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.1);
        -moz-box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.1);
        box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.1);
    }
    .links {
        font-size: 40px;

        li {
            a { color: $secondary-color; }
            a:hover { color: $primary-color; }
            a:active { color: $color5; }
        }
    }
}

#navigation-bar {
    nav {
        ul {
            li {
                a {
                    border-left: 1px solid $color1;
                    border-right: 1px solid $color1;
                    border-bottom: 1px solid $color1;
                    border-radius: 0 0 5px 5px;
                    padding: 10px 20px 10px 20px;
                    color: $color5;
                    background-color: $secondary-color;
                }
    
                a:hover {
                    color: $color1;
                    background-color: $primary-color;
                }

                a:active {
                    background-color: $color5;
                }
            }
        }
    }
}

form {
    display: block;
    border: 1px solid $border-color;
    border-radius: 5px;
    color: $color4;
    background-color: $second-background-color;
    max-width: 500px;
    padding: 10px 30px 30px 30px;

    .centered {
        text-align: center;
    }

    .required {
        color: rgb(124, 34, 34);
    }

    label {
        color: $color4;
        display: block;
        line-height: 2em;
        font-variant: small-caps;
    }

    input, textarea {
        display: block;
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        margin: 0;
        border: 1px solid $border-color;
        border-radius: 5px;
        font-size: 18px;
    }

    textarea {
        height: 100px;
    }

    button {
        font-size: 16px;
        font-weight: 500;
        border: 1px solid $background-color;
        border-radius: 5px;
        margin-top: 15px;
        padding: 10px 20px 10px 20px;
        width: 200px;
        color: $color1;
        background-color: $color2;
        font-variant: small-caps;
    }

    button:active {
        background-color: $color5;
    }

    #status {
        display: none;
        margin: 30px 0 10px 0;
        color: $color4;
    }

}

#contact-form {
    h3 {
        color: $color4;
        margin-bottom: 15px;
    }
}

.footer-wrapper {
    padding: 0 50px;
    margin-bottom: 20px;
}

#copyright {
    font-size: 12px;
}

.footer-links {
    font-size: 12px;
    text-align: right;

    li {
        padding: 2px;
    }

    a:hover { color: $color1; }
    a:active { color: $color2; }
}

/* ANIMATION */

// body * {
//     transition: 0.1s;
// }



/* RESPONSIVE */

@media screen and (max-width: 900px) {
    
    .wrapper {
        width: 90%;
    }

    .greeting-wrapper {
        grid-template-columns: 1fr;
        justify-items: center;
        align-content: flex-start;
        grid-template-areas:
            'greeting_item_left'
            'greeting_item_right';
    }

    .greeting-item-right {
        text-align: center;

        h1 {
            margin: 0;
        }
    }

    .links {
        justify-content: center !important;

        li {
            margin-left: 5px;
            margin-right: 5px;
        }
    }

    #contact-form {
        h3 {
            text-align: center;
        }
    }
    

}
